body {
  background-color: #111;
  font-family: Roboto, sans-serif;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 10px;
}

@media screen and (min-width: 320px), (max-width: 480) {
  .container {
    min-width: 320px;
    max-width: 428px;
    margin: auto;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

:root {
  --primary-brand-color: #000;
  --primary-text-color: #b7b7b7;
}

.search-section {
  background: var(--primary-brand-color);
  margin-top: 40px;
}

.search-form {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-bottom: 16px;
  display: flex;
}

.catalog-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.input-field {
  background-color: var(--primary-brand-color);
  width: 224px;
  height: 40px;
  color: var(--primary-text-color);
  letter-spacing: 0;
  border: 1px solid #fff;
  border-radius: 8px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.create-gallery {
  align-items: center;
  margin-bottom: 16px;
}

.gallery-section {
  background: var(--primary-brand-color);
  margin-bottom: 20px;
  position: relative;
}

.without-results-section {
  background: var(--primary-brand-color);
  margin-top: 48px;
}

.without-results {
  width: 280px;
  height: 120px;
  text-align: center;
  margin: 0 auto;
}

.without-results-text {
  color: #fff;
  margin: 0 auto;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.4;
}

.is-hidden {
  display: none;
}

.icon-search {
  width: 18px;
  height: 18px;
  fill: currentColor;
}

.flex-mobile {
  gap: 30px;
  display: flex;
}

.search-field {
  position: relative;
}

.input-field:focus {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 0 15px 5px #f87719;
}

.close-icon {
  cursor: pointer;
  background: none;
  border: rgba(0, 0, 0, 0);
  padding: 0;
  position: absolute;
  top: 25%;
  right: 24.5px;
}

.icon-clear-text {
  width: 9px;
  height: 9px;
  fill: #fff;
}

.icon-clear-text:-webkit-any(:hover, :focus) {
  fill: #f87719;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.icon-clear-text:is(:hover, :focus) {
  fill: #f87719;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .flex-mobile {
    gap: 16px;
  }

  .input-field {
    height: 44px;
  }

  .without-results {
    width: 513px;
    height: 104px;
    text-align: center;
  }

  .without-results-text {
    color: #fff;
    margin: 0 auto;
    font-size: 24px;
    font-weight: 400;
    line-height: 1.33;
  }

  .search-section {
    margin-top: 60px;
  }

  .search-form {
    margin-bottom: 36px;
  }

  .gallery-section {
    margin-bottom: 44px;
  }

  .create-gallery {
    flex-flow: wrap;
    gap: 28px 15px;
    margin-bottom: 16px;
    display: flex;
  }

  .close-icon {
    top: 28%;
  }
}

@media screen and (min-width: 1280px) {
  .without-results {
    width: 684px;
    height: 124px;
    text-align: center;
  }

  .without-results-text {
    color: #fff;
    margin: 0 auto;
    font-size: 32px;
    font-weight: 400;
    line-height: 1.13;
  }

  .search-section {
    margin-top: 80px;
  }

  .search-form {
    gap: 24px;
    margin-bottom: 48px;
  }

  .gallery-section {
    margin-bottom: 60px;
  }

  .create-gallery {
    row-gap: 32px;
    margin-bottom: 16px;
  }

  .input-field {
    height: 48px;
    font-size: 16px;
  }

  .flex-mobile {
    gap: 24px;
  }

  .close-icon {
    top: 30%;
  }
}

.no_scroll {
  overflow: hidden;
}

.footer {
  background-color: var(--primary-brand-color);
}

.footer_containet {
  min-width: 236px;
  max-width: 320px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 28px;
  display: flex;
}

.footer_copyright {
  text-align: center;
  color: #b7b7b7;
  align-items: center;
  margin-top: 0;
  margin-bottom: 8px;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.1;
  display: flex;
}

.footer_wraper {
  align-items: center;
  display: flex;
}

.wraper_descr {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  display: flex;
}

.wraper_text {
  text-align: center;
  color: #b7b7b7;
  align-items: center;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.19;
  display: flex;
}

.footer_icon {
  margin: 0 10px;
  animation: pulse .7s infinite alternate;
}

@keyframes pulse {
  from {
    transform: scale(.94);
  }

  to {
    transform: scale(1.5);
  }
}

.wraper_text_btn {
  margin-top: 0;
  margin-left: 5px;
}

.footer_btn_link {
  cursor: pointer;
  background: none;
  border: none;
  border-bottom: 2px solid #b7b7b7;
  padding: 0;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.footer_btn_link:hover, .footer_btn_link:focus {
  color: #f87719;
  border-color: #f87719;
  animation: pulses .8s infinite alternate;
}

@keyframes pulses {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(.9);
  }
}

.team_backdrop {
  width: 100%;
  height: 100%;
  z-index: 990;
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, .8);
  padding: 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), visibility .25s;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.team_wrapper {
  width: 320px;
  z-index: 999;
  opacity: 1;
  transform: matrix(1) translate(-50%) scale(1);
  background-color: rgba(222, 222, 222, .8);
  margin-left: auto;
  margin-right: auto;
  padding: 30px 15px;
  position: relative;
}

.team_modal_close_btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  display: flex;
  position: absolute;
  top: 5px;
  right: 5px;
}

.team_modal_close_icon {
  fill: currentColor;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.team_modal_close_icon:hover, .team_modal_close_icon:focus {
  fill: #f87719;
}

.team_title {
  text-align: center;
  text-transform: uppercase;
  color: #111;
  margin-bottom: 20px;
  font-size: 19px;
}

.team_list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 0;
  display: flex;
}

.team_item {
  background-color: #fff;
  border-radius: 3px;
  position: relative;
  overflow: hidden;
  box-shadow: 6px 6px 9px -1px rgba(0, 0, 0, .4);
}

.team_wrap {
  overflow: hidden;
}

.team_img {
  width: 200px;
  height: 180px;
  object-fit: cover;
  overflow: hidden;
}

.team_info {
  text-align: center;
  margin-bottom: 2px;
  padding: 10px;
  font-size: 13px;
  font-weight: 300;
}

.team_member {
  color: #111;
  margin-top: 10px;
  margin-bottom: 10px;
}

.team_role {
  color: #f87719;
  margin-top: 0;
  margin-bottom: 10px;
}

@media screen and (min-width: 768px) {
  .footer_containet {
    max-width: 768px;
    flex-direction: row;
    padding-top: 60px;
    padding-bottom: 40px;
    display: flex;
  }

  .footer_copyright {
    margin: 0 5px 0 0;
  }

  .wraper_descr, .wraper_text_btn {
    margin-bottom: 0;
  }

  .wraper_text {
    font-size: 16px;
    line-height: 1.19;
  }

  .team_wrapper {
    width: 580px;
  }

  .team_modal_close_btn {
    top: 10px;
    right: 10px;
  }

  .team_title {
    font-size: 26px;
  }
}

@media screen and (min-width: 1200px) {
  .footer_containet {
    padding-top: 96px;
    padding-bottom: 80px;
  }

  .team_wrapper {
    width: 1175px;
  }

  .team_modal_close_btn {
    top: 15px;
    right: 15px;
  }

  .team_title {
    font-size: 30px;
  }
}



:root {
  --primary-brand-color: #000;
  --primary-text-color: #b7b7b7;
}

header {
  background: var(--primary-brand-color);
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

body.is-open {
  overflow: hidden;
}

.header_container {
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo_text {
  color: var(--primary-text-color);
}

.logo_img {
  width: 30px;
  height: 24px;
}

.navbar_item_link {
  text-transform: uppercase;
  color: var(--primary-text-color);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mobile_menu_container {
  margin: 0 auto;
}

.backdrop.visual_hidden {
  opacity: 0;
}

.backdrop {
  opacity: .2;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: rgba(255, 255, 255, .8);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.visual_hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.mobile_menu_logo_img {
  width: 32px;
  height: 32px;
}

.nav_list-current {
  color: #f87719;
}

@media screen and (max-width: 767px) {
  .logo_text {
    display: none;
  }

  .header_menu_btn {
    text-transform: uppercase;
    color: #b7b7b7;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.16;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .mobile_menu_container {
    background-color: var(--primary-brand-color);
    height: 100%;
    z-index: 100;
    width: calc(100% - 116px);
    border-radius: 0 8px 8px 0;
    padding: 20px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-100%);
  }

  .mobile_menu_container.is-open {
    transform: translate(0);
  }

  .navbar {
    margin-top: 40px;
  }

  .navbar_list {
    flex-direction: column;
    gap: 24px;
    padding-left: 0;
    display: flex;
  }
}

#icon_moon, #icon_sun {
  width: 8px;
  height: 8px;
}

.header_theme_switch {
  cursor: pointer;
  width: 38px;
  height: 16px;
  background: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  border-radius: 194px;
  align-items: center;
  gap: 14px;
  padding: 5px;
  display: flex;
  position: relative;
}

#icon-moon, #icon-sun {
  width: 8px;
  height: 8px;
  fill: #fff;
}

@media screen and (min-width: 768px) {
  .header_container {
    padding-top: 12px;
    padding-bottom: 12px;
  }

  .mobile_menu_logo_img {
    display: none;
  }

  .logo_img {
    width: 48px;
    height: 48px;
    margin-right: 8px;
  }

  .logo_text {
    font-size: 24px;
    font-weight: 400px;
    line-height: 1.16;
    display: inline-block;
  }

  .navbar_list {
    font-size: 14px;
    font-weight: 500px;
    gap: 32px;
    line-height: 1.15;
    display: flex;
  }

  .header_menu_btn {
    display: none;
  }

  .header_theme_switch {
    width: 54px;
    height: 20px;
    gap: 24px;
    margin-left: auto;
  }

  #icon-moon, #icon-sun {
    width: 11px;
    height: 11px;
  }
}

@media screen and (min-width: 1200px) {
  .header_container {
    padding-top: 6px;
