:root {
  --modal-film-bgrcol: #111;
  --modal-film-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-film-backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal-film-item-img > img {
  display: block;
}

.modal-film-btn-close {
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 40px;
  right: 40px;
  transform: translate(50%, -50%);
}

.modal-film-btn-close:hover, .modal-film-btn-close:focus {
  background-color: rgba(157, 157, 159, .4);
}

.modal-film-btn-close:hover .modal-film-icon-close, .modal-film-btn-close:focus .modal-film-icon-close {
  fill: #b0aaaa;
}

.modal-film-icon-close {
  width: 15px;
  height: 15px;
  fill: var(--primary-about-text);
}

.modal-film-btn-space {
  width: 846px;
  height: 598px;
  background-color: var(--modal-film-bgrcol);
  box-shadow: var(--modal-film-shadow);
  scrollbar-width: none;
  scrollbar-color: gray;
  border-radius: 20px;
  padding: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
}

.modal-film-item-title > h3:first-child {
  color: var(--primary-title-color);
  margin: 22px 0 29px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.15;
}

.modal-film-item-title > h3 {
  color: var(--primary-vote-color);
  margin: 0 0 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.modal-film-item-title > p {
  text-transform: uppercase;
  color: var(--primary-vote-color);
  align-items: center;
  margin: 8px 0 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.modal-film-vote {
  margin-left: 62px;
}

.modal-film-votes, .modal-film-vote {
  text-align: center;
  color: var(--primary-vote-color-text);
  background: #f8f8f8;
  border-radius: 5px;
  padding: 1px 8px;
  font-size: 12px;
  line-height: 1.17;
}

.modal-film-popular {
  text-transform: uppercase;
  color: var(--primary-title-color);
  align-items: center;
  margin-left: 78px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.modal-film-genre {
  color: var(--primary-title-color);
  align-items: center;
  margin-left: 108px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.modal-film-item-img {
  width: 375px;
  height: 478px;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 1px 1px 24px 2px rgba(1, 1, 1, .45);
}

.modal-film-text {
  color: var(--primary-about-text);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.67;
