:root {
  --white-color: #fff;
  --black-color: #111;
  --grey-whiteTheme-color: #595959;
  --secondaryBlack-color: #282828;
}

body {
  background-color: var(--black-color);
}

.animation {
  transform: rotateZ(-.5turn);
}

.icon-arrow-down {
  stroke: var(--white-color);
  width: 12px;
  height: 8px;
  transition: all .4s;
}

.light-theme {
  background-color: var(--white-color);
}

.light-theme__text--grey {
  color: var(--grey-whiteTheme-color);
}

.light-theme__text--black {
  color: var(--black-color) !important;
}

.light-theme__section--secondaryText {
  color: var(--secondaryBlack-color);
}

.light-theme__input {
  color: #000;
  border-color: var(--grey-whiteTheme-color);
  background-color: var(--white);
}

.light-theme__search--icon {
  fill: var(--black-color);
  stroke: var(--black-color);
}

.light-theme__pageNumber {
  border-color: var(--grey-whiteTheme-color);
  color: var(--secondaryBlack-color);
}

.light-theme__modal {
  background-color: #fff;
  box-shadow: 1px 1px 14px 4px rgba(0, 0, 0, .22);
}

.light-theme__modal--closeIcon {
  stroke: var(--secondaryBlack-color);
}

.light-theme__modal--btn {
  color: var(--black-color);
}

.theme-switch {
  width: 60px;
  height: 24px;
  margin-left: auto;
  display: inline-block;
  position: relative;
}

.theme-switch__checkbox {
  display: none;
}

.theme-switch__slider {
  cursor: pointer;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 34px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.theme-switch__icon--round {
  width: 18px;
  height: 18px;
  z-index: 2;
  transition: all .4s;
  position: absolute;
  top: 3px;
  left: 39px;
}

.theme-switch__icon--moon {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 5px;
  left: 4px;
}

.theme-switch__icon--sun {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 4px;
  right: 4px;
}

.theme-switch__checkbox:checked + .theme-switch__icon--round {
  left: 3px;
  transform: rotateZ(-.5turn);
}

@media screen and (max-width: 768px) {
  .theme-switch {
    width: 44px;
    height: 18px;
  }

  .theme-switch__icon--moon {
    width: 11px;
    height: 11px;
    top: 4px;
    left: 3px;
  }

  .theme-switch__icon--sun {
    width: 11px;
    height: 11px;
  }

  .theme-switch__icon--round {
    width: 13px;
    height: 13px;
    top: 2.5px;
    left: 28.5px;
  }
}

